  <div class="news">
    <div class="banner">
      <img :src="bannerUrl" class="banner-img" />
    </div>
    <el-row>
      <el-col v-for="(row, index) in rows" :key="index" :offset="4" :span="16" style="margin-top:20px;">
        <el-card>
          <template v-slot:header>
            <div class="card-header">
              <h3>{{row.title}}</h3>
            </div>
          </template>
          <el-row>
            <el-col v-if="row.images" :span="24">
              <div>
                <el-carousel :interval="2000" type="card" height="300px" class="carousel">
                  <el-carousel-item v-for="url in row.images" :key="url">
                    <img :src="url" height="500" />
                  </el-carousel-item>
                </el-carousel>
                <div class="card-content" v-html="row.content">
                </div>
              </div>
            </el-col>
            <template v-else>
              <el-col :span="12">
                <div>
                  <img v-if="row.image" :src="row.image" class="image" style="height:500px;" />
                  <img height="500" v-else :src="defaultImgUrl" />
                </div>
              </el-col>
              <el-col :span="12">
                <div class="card-content" v-html="row.content">
                </div>
              </el-col>
            </template>
          </el-row>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data () {
    return {
      defaultImgUrl: require('../assets/image/default.jpg'),
      rows: [],
      bannerUrl: require('../assets/image/banner.png')
    }
  },
  created () {
    this.$http.get('./json/news.json').then(res => {
      this.rows = res.data
    })
  }
}
</script>
